export interface IPromiseData<T> {
    promise: Promise<T>;
    resolver: (value: T | PromiseLike<T>) => void;
    reject: (reason?: any) => void;
    startTimer: (timeout: number) => void;
}

//some platforms have syncronous resolve problem (ex: IOS JavascriptCore)
declare var doNothing: (() => void) | undefined;
let syncResolved = false as boolean;
new Promise<void>(res => res()).then(() => syncResolved = true);
typeof(doNothing) != "undefined" && doNothing();
const syncResolveProblem = syncResolved === true; // platform has sync resolve problem or not

type ResolveFunc<T> = (value: T | PromiseLike<T>) => void;
/**
 * @param timeout timeout time in ms, undefined if infinite
 */
export function CreatePromiseData<T>(timeout?: number, opt: { fixSyncResolve?: boolean } = {}): IPromiseData<T> {
    const syncResolveFixer = (cb: ResolveFunc<T>) => syncResolveProblem && opt.fixSyncResolve ? (value: T | PromiseLike<T>) => setTimeout(() => cb(value)) : cb;
    let resolver: ResolveFunc<T> = <any>undefined, reject: (reason?: any) => void = <any>undefined;
    let timer: number;
    const startTimer: IPromiseData<T>["startTimer"] = (timeout: number) => {
        if (timer) { clearTimeout(timer as unknown as NodeJS.Timeout); }
        timer = setTimeout(() => { reject(new Error(`The operation has timed out. timeoutValue  : ${timeout}`)); }, timeout) as unknown as number;
    }

    if (timeout) {
        startTimer(timeout);
    }

    return {
        startTimer,
        promise: new Promise<T>((res, rej) => { resolver = syncResolveFixer(res); reject = rej; }),
        resolver,
        reject
    };
}